.resume {
  margin: 0 auto;
  /* border: 1px solid red; */
}
.resume .items {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 1em 0;
  padding: 0.5em;
  font-size: 1.2em;
  border: 2px solid #db952377;
  border-radius: 10px;
  box-shadow: 10px 10px 5px #DB9423;
}
.resume .items figure.imgPizza {
  margin-right: 0.5em;
  width: 3.5em;
  height: auto;
  overflow: hidden;
  /* border: 1px solid red; */
}
.resume .items figure.imgPizza img {
  max-width: 100%;
  height: auto;
  display: block;
  /* border: 1px solid red; */
}
.resume .pizza {
  display: block;
  /* flex-wrap: wrap; */
  width: 6em;
  /* border: 1px solid red; */
}
.resume .pizza .size {
  display: block;
}
.resume .cantidad {
  margin: 0 1em;
  width: 1.5em;
  /* border: 1px solid red; */
}
.resume .precio {
  display: none;
}
.resume .precioCantidad {
  font-weight: 600;
  width: 6em;
  text-align: right;
  /* border: 1px solid red; */
}
.resume .hrElementUp {
  margin-top: 2em;
}
.resume .aSumar {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.resume .subTotal,
.resume .envioPrecio {
  margin: 0.5em 0 0.5em 0;
  text-align: right;
  font-size: 1.2em;
  font-weight: 400;
  /* color: #DB9423; */
  /* border: 1px solid red; */
}
.resume .aSumar .number {
  padding-right: 10px;
  width: 6em;
  font-weight: 700;
  font-size: 1.2em;
  text-align: right;
  /* border: 1px solid red; */
}
.resume .aSumarTotal {
  display: flex;
  justify-content: space-between;
}
.resume .aSumarTotal .totalLetra,
.resume .aSumarTotal .totalNumber {
  margin: 1em 0;
  font-size: 2em;
  font-weight: 800;
  color: #DB9423;
}
.resume .aSumarTotal .totalNumber {
  padding-right: 10px;
}
/* ********************************************************************************************QUERYTABLET */
@media only screen and (max-width: 1024px) { 
  .resume .pizza {
    display: flex;
    /* flex-wrap: wrap; */
    width: 10em;
    /* border: 1px solid red; */
  }
}
/* ********************************************************************************************QUERYPHONE */
@media only screen and (max-width: 480px) {
  .resume .items {
    font-size: 1em;
  }
  .resume .pizza {
    display: block;
    width: 6em;
    text-align: center;
  }
  .resume .cantidad {
    margin: 0 0.5em;
    width: 1.5em;
  }
  .resume .precioCantidad {
    width: 5em;
  }
  .resume .subTotal,
  .resume .envioPrecio {
    font-size: 1em;
  }
  .resume .aSumar .number {
    font-size: 1em;
  }
  .resume .aSumarTotal .totalLetra,
  .resume .aSumarTotal .totalNumber {
    font-size: 1.8em;
  }
}