.pago {
  width: 90%;
  margin: 4em auto;
}
.pago .sNavigation {
  margin-bottom: 2.5em;
  text-align: center;
  font-size: 1.5em;
  /* border: 1px solid red; */
}
.pago .sNavigation .breadcrumb > li {
  display: inline;
}
.pago .sNavigation .breadcrumb li.pago::before {
  color: black;
}
.pago .sNavigation .breadcrumb li+li::before {
  padding: 10px;
  content: ">";
}
.pago .sNavigation .breadcrumb .pagoBreadCrumb {
  color: #DB9423;
  font-weight: 500;
}
.pago .loading {
  display: flex;
  justify-content: center;
  align-items: center;
}
.pago .loading h2 { 
  font-size: 2em;
  animation: opacity 1s ease-in-out infinite alternate;
} 
@keyframes opacity{
  to {
    opacity: 1;
  }
  from {
    opacity: 0;
  }
}
/* This style is from and for Lyra library */
.pago .container {
  display: flex; 
  justify-content: center; 
}
.pago .vacio {
  margin: 3em auto;
  text-align: center;
}
.pago .vacio a {
  color: #DB9423;
  text-decoration: none;
}
.pago .vacio a:hover {
  color: #db5d23;
}
.pago .form h2 {
  margin-bottom: 1em;
  text-align: center;
}
.kr-embedded .kr-modal-wrapper .kr-modal .kr-modal-footer button, .kr-embedded .kr-payment-button {
  background-color: #DB9423 !important;
}
.kr-embedded .kr-modal-wrapper .kr-modal .kr-modal-footer button, .kr-embedded .kr-payment-button span {
  font-weight: 500 !important;
  color: black !important ;
}
.pago p.recomendacion {
  margin-top: 2em;
  text-align: center;
  font-size: 0.75em;
  /* border: 1px solid red; */
}
/* ********************************************************************************************QUERYPHONE */
@media only screen and (max-width: 480px) {
  .pago .loading h2 {
    font-size: 1.5em;
  }
  .pago .sNavigation {
    font-size: 1.1em;
    /* border: 1px solid red; */
  }
  .pago p.recomendacion {
    font-size: 0.6em;
  }
}
