.animatedDots div {
  margin: 1.5em 0.5em 0.5em 0.5em;
  height: 15px;
  width: 15px;
  border-radius:50%;
  transform: scale(0);
  background-color:red;
  animation: scaling 2s ease-in-out infinite alternate;
  display: inline-block;
}

@keyframes scaling{
  /* to {
    transform: scale(1);
    background-color: #cd863f;
  }
  from {
    transform: scale(0);
    background-color: #db952377;
  } */
  0%{
    transform: scale(0);
    background-color: #db952377;
  }
  40%{
    transform: scale(1);
    background-color: #DB9423;
  }
  50%{
    transform: scale(1);
    background-color: #cd863f;
  }
}

div:nth-child(0){
  animation-delay:0s;
}
div:nth-child(1){
  animation-delay:0.3s;
}
div:nth-child(2){
  animation-delay:0.6s;
}
div:nth-child(3){
  animation-delay:1s;
}

@media only screen and (max-width: 480px) {
  .animatedDots div {
    margin: 1.3em 0.5em 0.5em 0.5em;
    height: 7.5px;
    width: 7.5px;
  } 
}