.tradicionales {
  margin: 4em auto;
}
.tradicionales .loading {
  display: flex;
  justify-content: center;
}
.tradicionales .loading h2 {
  font-size: 2em;
  animation: opacity 1s ease-in-out infinite alternate;
}
@keyframes opacity{
  to {
    opacity: 1;
  }
  from {
    opacity: 0;
  }
}
.tradicionales .tradicionalesInside {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 4em 2.5em;
  margin: 4em auto;
  width: 90%;
  text-align: center;
  /* border: 1px solid red; */
}
.tradicionales .tradicionalesInside .Margarita {
  grid-column: 1 / span 2;
}
.tradicionales .tradicionalesInside .Marinara {
  grid-column: 3 / span 2;
}
.tradicionales .tradicionalesInside .Napolitana {
  grid-column: 5 / span 2;
}
.tradicionales .tradicionalesInside .Pepperoni {
  grid-column: 1 / span 2;
}
.tradicionales .tradicionalesInside .Estaciones {
  grid-column: 3 / span 2;
}
.tradicionales .tradicionalesInside .Americana {
  grid-column: 5 / span 2;
}
.tradicionalesInside .Hawaiana {
  grid-column: 1 / span 6;
}
.tradicionales .tradicionalesInside h2 {
  margin: 0 0 1em 0;
}
.tradicionales .tradicionalesInside .details {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.5em auto;
  /* border: 1px solid red; */
}
.tradicionales .tradicionalesInside .details p.precio {
  text-align: right;
}
.tradicionales .tradicionalesInside .details p.hyphen {
  margin: auto 5px;
}
.tradicionales .tradicionalesInside .details p.size {
  margin: auto 1em auto 0;
  width: 4em;
  text-align: left;
  /* border: 1px solid red; */
}
.tradicionales .tradicionalesInside .details button {
  margin: 0 0.7em;
  background-color: rgb(228, 227, 227);
  border: none;
  outline: none;
  box-shadow: 0 0 5PX #DB9423;
}
.tradicionales .tradicionalesInside .details button:hover {
  cursor: pointer;
  box-shadow: inset 0 0 5PX #DB9423;
}
.tradicionales .tradicionalesInside .details button.mas {
  padding: 0 0.5em;
}
.tradicionales .tradicionalesInside .details button.menos {
  padding: 0 0.6em;
}
.tradicionales .tradicionalesInside .details p.counter {
  margin: 0 0 0 0.5em;
  padding: 0;
  width: 2.6em;
  text-align: center;
  font-weight: 800;
  color: #db5d23;
  /* border: 1px solid red; */
}
.tradicionales .tradicionalesInside .description {
  margin: 1em 0 0 0;
}
.tradicionales .tradicionalesInside figure.imgPizza {
  margin: 0 auto;
  width: 15em;
  height: auto;
  overflow: hidden;
  /* border: 1px solid red; */
}
.tradicionales .tradicionalesInside figure.imgPizza img {
  max-width: 100%;
  height: auto;
  display: block;
  /* border: 1px solid red; */
}
.tradicionales div {
  text-align: center;
}
.tradicionales a .botonCarrito {
  font-size: 1.2em;
  letter-spacing: 0.05em;
  margin: 0 auto 2em auto;
  padding: 0.7em 1.5em;
  border-width: 3px;
  border-radius: 0.5em;
  color: white;
  outline: none;
  background-color: #DB9423;
}
.tradicionales .botonCarrito:hover {
  cursor: pointer;
  background-color: #DB9423;
}
/* ************************************************************************QUERYTablet */
@media only screen and (max-width: 1050px) {
  .tradicionales .tradicionalesInside {
    grid-gap: 3em 1.5em;
  }
  .tradicionales .tradicionalesInside .Margarita {
    grid-column: 1 / span 3;
  }
  .tradicionales .tradicionalesInside .Marinara {
    grid-column: 4 / span 3;
  }
  .tradicionales .tradicionalesInside .Napolitana {
    grid-column: 1 / span 3;
  }
  .tradicionales .tradicionalesInside .Pepperoni {
    grid-column: 4 / span 3;
  }
  .tradicionales .tradicionalesInside .Estaciones {
    grid-column: 1 / span 3;
  }
  .tradicionales .tradicionalesInside .Americana {
    grid-column: 4 / span 3;
  }
  .tradicionalesInside .Hawaiana {
    grid-column: 1 / span 6;
  }
  .tradicionales .tradicionalesInside .details {
    margin: 2em auto;
  }
  .tradicionales .tradicionalesInside .details button {
    font-size: 1em;
  }
  .tradicionales .tradicionalesInside .details button.mas {
    padding: 0.5em 1em;
  }
  .tradicionales .tradicionalesInside .details button.menos {
    padding: 0.5em 1.1em;
  }
}
/* ************************************************************************QUERYMIDLETABLET */
@media only screen and (max-width: 790px) {
  .tradicionales .tradicionalesInside {
    grid-gap: 3em 1.5em;
  }
  .tradicionales .tradicionalesInside .Margarita {
    grid-column: 1 / span 6;
  }
  .tradicionales .tradicionalesInside .Marinara {
    grid-column: 1 / span 6;
  }
  .tradicionales .tradicionalesInside .Napolitana {
    grid-column: 1 / span 6;
  }
  .tradicionales .tradicionalesInside .Pepperoni {
    grid-column: 1 / span 6;
  }
  .tradicionales .tradicionalesInside .Estaciones {
    grid-column: 1 / span 6;
  }
  .tradicionales .tradicionalesInside .Americana {
    grid-column: 1 / span 6;
  }
  .tradicionalesInside .Hawaiana {
    grid-column: 1 / span 6;
  }
  .tradicionales .tradicionalesInside .details {
    /* flex-direction: column; */
    margin: 2em auto;
  }
  .tradicionales .tradicionalesInside .details button {
    margin: 0 0.6em;
  }
  .tradicionales .tradicionalesInside .details p.counter {
    margin: 0 0 0 0.4em;
  }
}
/* ************************************************************************QUERYPHONE */
@media only screen and (max-width: 480px) {
  .tradicionales h2 {
    font-size: 1.5em;
  }
}
@media only screen and (max-width: 372px) {
  .tradicionales .tradicionalesInside .details {
    /* flex-direction: column-reverse; */
    flex-wrap: wrap-reverse;
    width: 97%;
  }
  .tradicionales .tradicionalesInside .details p.counter {
    margin: 0 0 1em 0;
  }
}