.bebidas {
  margin: 4em auto;
}
.bebidas .loading {
  display: flex;
  justify-content: center;
}
.bebidas .loading h2 {
  font-size: 2em;
  animation: opacity 1s ease-in-out infinite alternate;
}
@keyframes opacity{
  to {
    opacity: 1;
  }
  from {
    opacity: 0;
  }
}
.bebidas .bebidasInside {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 4em 2.5em;
  margin: 4em auto;
  width: 90%;
  text-align: center;
  /* border: 1px solid red; */
}
[class = 'Inca Kola Original 500 ml'] {
  order: 1;
  grid-column: 1 / span 2;
}
[class="Inca Kola Zero 500 ml"] {
  order: 2;
  grid-column: 3 / span 2;
}
[class="Inca Kola Original 1.5L"] {
  order: 3;
  grid-column: 5 / span 2;
}
[class="Inca Kola Zero 1.5L"] {
  order: 4;
  grid-column: 1 / span 2;
}
[class="Coca Cola Original 500 ml"] {
  order: 5;
  grid-column: 3 / span 2;
}
[class="Coca Cola Zero 500 ml"] {
  order: 6;
  grid-column: 5 / span 2;
}
[class="Coca Cola Original 1.5L"] {
  order: 7;
  grid-column: 1 / span 2;
}
[class="Coca Cola Zero 1.5L"] {
  order: 8;
  grid-column: 3 / span 2;
}
[class="Cerveza Cusqueña 330 ml"] {
  order: 9;
  grid-column: 5 / span 2;
}
[class = 'Agua Mineral 600 ml'] {
  order: 10;
  grid-column: 3 / span 2;
}
.bebidasInside h2 {
  margin: 1em 0 0 0;
}
.bebidasInside .details {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.5em auto;
  /* border: 1px solid red; */
}
.bebidasInside .details p.precio {
  margin-right: 0.5em;
  text-align: right;
}
.bebidasInside .details button {
  margin: 0 0.7em;
  background-color: rgb(228, 227, 227);
  border: none;
  outline: none;
  box-shadow: 0 0 5PX #DB9423;
}
.bebidasInside .details button:hover {
  cursor: pointer;
  box-shadow: inset 0 0 5PX #DB9423;
}
.bebidasInside .details button.mas {
  padding: 0 0.5em;
}
.bebidasInside .details button.menos {
  padding: 0 0.6em;
}
.bebidasInside .details p.counter {
  margin: 0 0 0 0.5em;
  padding: 0;
  width: 2.6em;
  text-align: center;
  font-weight: 800;
  color: #db5d23;
  /* border: 1px solid red; */
}
.bebidasInside figure.imgPizza {
  margin: 0 auto;
  width: 15em;
  height: auto;
  overflow: hidden;
  /* border: 1px solid red; */
}
.bebidas .bebidasInside figure.imgPizza img {
  max-width: 100%;
  height: auto;
  display: block;
  /* border: 1px solid red; */
}
.bebidas .size {
  margin: 0 0 1em 0;
}
.bebidas div {
  text-align: center;
}
.bebidas a .botonCarrito {
  font-size: 1.2em;
  letter-spacing: 0.05em;
  margin: 0 auto 2em auto;
  padding: 0.7em 1.5em;
  border-width: 3px;
  border-radius: 0.5em;
  color: white;
  outline: none;
  background-color: #DB9423;
}
.bebidas .botonCarrito:hover {
  cursor: pointer;
  background-color: #DB9423;
}
/* ************************************************************************QUERYTablet */
@media only screen and (max-width: 1050px) {
  .bebidasInside {
    grid-gap: 3em 1.5em;
  }
  [class = 'Inca Kola Original 500 ml'] {
    grid-column: 1 / span 3;
  }
  [class="Inca Kola Zero 500 ml"] {
    grid-column: 4 / span 3;
  }
  [class="Inca Kola Original 1.5L"] {
    grid-column: 1 / span 3;
  }
  [class="Inca Kola Zero 1.5L"] {
    grid-column: 4 / span 3;
  }
  [class="Coca Cola Original 500 ml"] {
    grid-column: 1 / span 3;
  }
  [class="Coca Cola Zero 500 ml"] {
    grid-column: 4 / span 3;
  }
  [class="Coca Cola Original 1.5L"] {
    grid-column: 1 / span 3;
  }
  [class="Coca Cola Zero 1.5L"] {
    grid-column: 4 / span 3;
  }
  [class="Cerveza Cusqueña 330 ml"] {
    grid-column: 1 / span 3;
  }
  [class = 'Agua Mineral 600 ml'] {
    grid-column: 4 / span 3;
  }
  .bebidasInside .details {
    margin: 2em auto;
  }
  .bebidasInside .details button {
    font-size: 1em;
  }
  .bebidasInside .details button.mas {
    padding: 0.5em 1em;
  }
  .bebidasInside .details button.menos {
    padding: 0.5em 1.1em;
  }
}
/* ************************************************************************QUERYMIDLETABLET */
@media only screen and (max-width: 790px) {
  .bebidasInside {
    grid-gap: 3em 1.5em;
  }
  [class = 'Inca Kola Original 500 ml'] {
    grid-column: 1 / span 6;
  }
  [class="Inca Kola Zero 500 ml"] {
    grid-column: 1 / span 6;
  }
  [class="Inca Kola Original 1.5L"] {
    grid-column: 1 / span 6;
  }
  [class="Inca Kola Zero 1.5L"] {
    grid-column: 1 / span 6;
  }
  [class="Coca Cola Original 500 ml"] {
    grid-column: 1 / span 6;
  }
  [class="Coca Cola Zero 500 ml"] {
    grid-column: 1 / span 6;
  }
  [class="Coca Cola Original 1.5L"] {
    grid-column: 1 / span 6;
  }
  [class="Coca Cola Zero 1.5L"] {
    grid-column: 1 / span 6;
  }
  [class="Cerveza Cusqueña 330 ml"] {
    grid-column: 1 / span 6;
  }
  [class = 'Agua Mineral 600 ml'] {
    grid-column: 1 / span 6;
  }
  .bebidasInside .details button {
    margin: 0 0.6em;
  }
  .bebidasInside .details p.counter {
    margin: 0 0 0 0.4em;
  }
}
/* ************************************************************************QUERYPHONE */
@media only screen and (max-width: 480px) {
  .bebidas h2 {
    font-size: 1.5em;
  }
}