footer {
  padding: 2em 0 4em 0;
  background-image: linear-gradient(to bottom, rgba(12, 12, 12, 0.7), rgba(0, 0, 0, 1)), 
    url(../../images/backgroundFooter.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
footer h3 {
  font-size: 2.5em;
}
footer p {
  margin: 0 auto;
  width: 70%;
}
footer h4 {
  margin: auto;
  width: 90%;
  font-size: 1.5em;
}
footer h3, footer p, footer h4 {
  text-align: center;
  color: white;
  /* border: 1px red solid; */
}
footer figure.divisor{
  display: flex;
  justify-content: center;
  margin: 1.5em;
}
footer div.divButton {
  text-align: center;  
  /* border: 1px red solid; */
}
footer a button {
  font-size: 1.2em;
  letter-spacing: 0.05em;
  margin: 2em auto;
  padding: 0.7em 1.5em;
  border-width: 3px;
  border-radius: 0.5em;
  color: white;
  outline: none;
  background-color: #DB9423;
  /* outline:none; */
  /* border: 5px none; */
  /* border: 1px red solid; */
}
footer button:hover {
  cursor: pointer;
  background-color: #db5d23;
}
footer section.socialIcons {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  align-content: center;
  /* border: 1px red solid; */
}
footer section.socialIcons figure {
  margin: 5em 0.5em;
  height: auto;
  width: 2.5em;
  overflow: hidden;
  /* border: 1px red solid; */
}
footer section.socialIcons figure img {
  max-width: 100%;
  height: auto;
  display: block;
  border: 2px dotted white;
  border-radius: 100%;
}
footer section.copyRight {
  display: flex;
  justify-content: space-between;
}
footer section.copyRight p {
  color: #929292;
}
/* ************************************************************************QUERYTABLET */
@media only screen and (max-width: 1024px) {
  footer section.copyRight {
    display: flex;
    flex-direction: column;
  }
  footer section.copyRight p {
    width: 95%;
  }
  footer section.copyRight > :first-child {
    margin: 1em auto;
  }
}
/* ************************************************************************QUERYPHONE */
@media only screen and (max-width: 480px) {
  footer {
    padding: 2em 0 2em 0;
  }
}@media only screen and (max-width: 372px) {
  footer h4 {
    font-size: 1.3em;
  }
}