.deLaCasa {
  margin: 4em auto;
}
.deLaCasa .loading {
  display: flex;
  justify-content: center;
}
.deLaCasa .loading h2 {
  font-size: 2em;
  animation: opacity 1s ease-in-out infinite alternate;
}
@keyframes opacity{
  to {
    opacity: 1;
  }
  from {
    opacity: 0;
  }
}
.deLaCasa .deLaCasaInside {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 4em 2.5em;
  margin: 4em auto;
  width: 90%;
  text-align: center;
  /* border: 1px solid red; */
}
.deLaCasaInside .Zarela {
  grid-column: 1 / span 2;
}
.deLaCasaInside .All {
  grid-column: 3 / span 2;
}
.deLaCasaInside .Lomazo {
  grid-column: 5 / span 2;
}
.deLaCasaInside .Veggie {
  grid-column: 1 / span 2;
}
.deLaCasaInside .Divina {
  grid-column: 3 / span 2;
}
.deLaCasaInside .BBQ {
  grid-column: 5 / span 2;
}
.deLaCasaInside .Especial {
  grid-column: 3 / span 2;
}
.deLaCasaInside h2 {
  margin: 0 0 1em 0;
}
.deLaCasaInside .details {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.5em auto;
  /* border: 1px solid red; */
}
.deLaCasaInside .details p.precio {
  text-align: right;
}
.deLaCasaInside .details p.hyphen {
  margin: auto 5px;
}
.deLaCasaInside .details p.size {
  margin: auto 1em auto 0;
  width: 4em;
  text-align: left;
  /* border: 1px solid red; */
}
.deLaCasaInside .details button {
  margin: 0 0.7em;
  background-color: rgb(228, 227, 227);
  border: none;
  outline: none;
  box-shadow: 0 0 5PX #DB9423;
}
.deLaCasaInside .details button:hover {
  cursor: pointer;
  box-shadow: inset 0 0 5PX #DB9423;
}
.deLaCasaInside .details button.mas {
  padding: 0 0.5em;
}
.deLaCasaInside .details button.menos {
  padding: 0 0.6em;
}
.deLaCasaInside .details p.counter {
  margin: 0 0 0 0.5em;
  padding: 0;
  width: 2.6em;
  text-align: center;
  font-weight: 800;
  color: #db5d23;
  /* border: 1px solid red; */
}
.deLaCasaInside .description {
  margin: 1em 0 0 0;
}
.deLaCasaInside figure.imgPizza {
  margin: 0 auto;
  width: 15em;
  height: auto;
  overflow: hidden;
  /* border: 1px solid red; */
}
.deLaCasa .deLaCasaInside figure.imgPizza img {
  max-width: 100%;
  height: auto;
  display: block;
  /* border: 1px solid red; */
}
.deLaCasa div {
  text-align: center;
}
.deLaCasa a .botonCarrito {
  font-size: 1.2em;
  letter-spacing: 0.05em;
  margin: 0 auto 2em auto;
  padding: 0.7em 1.5em;
  border-width: 3px;
  border-radius: 0.5em;
  color: white;
  outline: none;
  background-color: #DB9423;
}
.deLaCasa .botonCarrito:hover {
  cursor: pointer;
  background-color: #DB9423;
}
/* ************************************************************************QUERYTablet */
@media only screen and (max-width: 1050px) {
  .deLaCasaInside {
    grid-gap: 3em 1.5em;
  }
  .deLaCasaInside .Zarela {
    grid-column: 1 / span 3;
  }
  .deLaCasaInside .All {
    grid-column: 4 / span 3;
  }
  .deLaCasaInside .Lomazo {
    grid-column: 1 / span 3;
  }
  .deLaCasaInside .Veggie {
    grid-column: 4 / span 3;
  }
  .deLaCasaInside .La {
    grid-column: 1 / span 3;
  }
  .deLaCasaInside .BBQ {
    grid-column: 4 / span 3;
  }
  .deLaCasaInside .Especial {
    grid-column: 1 / span 6
  }
  .deLaCasaInside .details {
    margin: 2em auto;
  }
  .deLaCasaInside .details button {
    font-size: 1em;
  }
  .deLaCasaInside .details button.mas {
    padding: 0.5em 1em;
  }
  .deLaCasaInside .details button.menos {
    padding: 0.5em 1.1em;
  }
}
/* ************************************************************************QUERYMIDLETABLET */
@media only screen and (max-width: 790px) {
  .deLaCasaInside {
    grid-gap: 3em 1.5em;
  }
  .deLaCasaInside .Zarela {
    grid-column: 1 / span 6;
  }
  .deLaCasaInside .All {
    grid-column: 1 / span 6;
  }
  .deLaCasaInside .Lomazo {
    grid-column: 1 / span 6;
  }
  .deLaCasaInside .Veggie {
    grid-column: 1 / span 6;
  }
  .deLaCasaInside .La {
    grid-column: 1 / span 6;
  }
  .deLaCasaInside .BBQ {
    grid-column: 1 / span 6;
  }
  .deLaCasaInside .Especial {
    grid-column: 1 / span 6
  }
  .deLaCasaInside .details {
    /* flex-direction: column; */
    margin: 2em auto;
  }
  .deLaCasaInside .details button {
    margin: 0 0.6em;
  }
  .deLaCasaInside .details p.counter {
    margin: 0 0 0 0.4em;
  }
}
/* ************************************************************************QUERYPHONE */
@media only screen and (max-width: 480px) {
  .deLaCasa h2 {
    font-size: 1.5em;
  }
}
@media only screen and (max-width: 372px) {
  .deLaCasaInside .details {
    /* flex-direction: column-reverse; */
    flex-wrap: wrap-reverse;
    width: 97%;
  }
  .deLaCasaInside .details p.counter {
    margin: 0 0 1em 0;
  }
}