.PoliticaAlimentos {
  display: flex;
  justify-content: space-around;
  margin: 5em auto;
  width: 90%;
  /* border: 1px solid red; */
}
.PoliticaAlimentos .info {
  margin: auto 0;
  width: 50%;
  text-align: left;
  /* border: 1px solid red; */
}
.PoliticaAlimentos .info h2 {
  font-size: 2.5em;
  /* border: 1px solid red; */
}
.PoliticaAlimentos .info p {
  margin-right: 1em;
  /* border: 1px solid red; */
}
.PoliticaAlimentos .imagen {
  margin: auto 0;
  /* border: 1px solid red; */
}
.PoliticaAlimentos .imagen figure {
  /* margin: auto; */
  width: 100%;
  height: auto;
  overflow: hidden;
  /* border: 1px solid red; */
}
.PoliticaAlimentos .imagen img {
  max-width: 100%;
  height: auto;
  display: block;
  /* border: 1px solid red; */
}
/* ************************************************************************QUERYmiddleTablet */
@media only screen and (max-width: 768px) {
  .PoliticaAlimentos {
    display: block;
  }
  .PoliticaAlimentos .info {
    margin-bottom: 2em;
    width: 100%;
    text-align: justify;
    /* border: 1px solid red; */
  }
  .PoliticaAlimentos .info h2, .PoliticaAlimentos .info h3, .PoliticaAlimentos .info .divisor {
    text-align: center;
  }
  .PoliticaAlimentos .imagen figure {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 70%;
  }
  .PoliticaAlimentos .info p {
    margin-right: 0;
    /* border: 1px solid red; */
  }
}
/* ************************************************************************QUERYPHONE */
@media only screen and (max-width: 480px) {
  .PoliticaAlimentos {
    margin: 1em auto 4em auto;
    /* border: 1px solid red; */
  }
  .PoliticaAlimentos .info h2 {
    font-size: 1.5em;
  }
}