.chefs {
  margin: 4em auto;
  text-align: center;
}
.chefs h3 {
  margin: 1em auto;
  width: 90%;
  font-size: 2.5em;
  /* border: 1px solid red; */
}
.chefs h4 {
  margin: 0 auto;
  font-size: 1.5em;
  width: 90%;
  /* border: 1px solid red; */
}
.chefs p {
  margin: 0 auto;
  width: 90%;
  /* border: 1px solid red; */
}
.chefs figure.imgJuan {
  margin: 3em auto 1em auto;
  border-radius: 2em;
  width: 10em;
  height: auto;
  overflow: hidden;
  box-shadow: 0 0 15PX #000000;
}
.chefs figure.imgRaul {
  margin: 3em 0 1em 0;
  width: 100%;
  height: auto;
  overflow: hidden;
}
.chefs figure.imgJuan img, .chefs figure.imgJuan img {
  margin: 0 auto;
  max-width: 100%;
  height: auto;
  display: block;
}