section.portadaPizzas {
  padding: 4em 0 5em 0;
  text-align: center;
  background-image: linear-gradient(to bottom, rgba(117, 117, 117, 0.671), rgba(0, 0, 0, 0.85)),
  url(../../../images/portadas/pizzarelaPizzas.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
section.portadaPizzas h1 {
  margin: 0 0 0.5em 0;
  font-size: 6em;
  color: white;
  /* border: 1px solid red; */
}
section.portadaPizzas p {
  margin: auto;
  width: 90%;
  color: white;
  /* border: 1px solid red; */
}
@media only screen and (max-width: 790px) {
  section.portadaPizzas h1.Tradicionales,
  section.portadaPizzas h1.Complementos,
  section.portadaPizzas h1.Promociones {
    font-size: 4em;
  }
}
/* ************************************************************************QUERYPHONE */
@media only screen and (max-width: 480px) {
  section.portadaPizzas {
    padding: 3em 0 5em 0;
  }
  section.portadaPizzas h1 {
    /* margin: 0.5em 0; */
    font-size: 3.5em;
    /* border: 1px solid red; */
  }
  section.portadaPizzas h1.Tradicionales,
  section.portadaPizzas h1.Promociones {
    font-size: 3em;
  }
  section.portadaPizzas h1.Complementos {
    font-size: 2.5em;
  }
}