.respuesta {
  width: 90%;
  margin: 3em auto;
  /* text-align: center; */
}
.respuesta h2 {
  margin-bottom: 0.5em;
}
.respuesta p {
  margin: 0.2em 0;
}
.respuesta .detalleRespuesta,
.respuesta .detalleProductos,
.respuesta .infoCliente {
  margin: 3em 0;
  padding: 1em;
  border: 2px solid #DB9423;
  border-radius: 20px;
  box-shadow: 0 0 5px peru;
}
.respuesta section.infoCliente {
  /* margin: 3em 0; */
  display: flex;
  padding: 0;
  border: 0;
  box-shadow: 0 0 0 white;
}
.respuesta section.infoCliente section {
  margin-top: 0;
  margin-bottom: 0;
  padding: 1em;
  border: 0;
  border: 2px solid #DB9423;
  border-radius: 20px;
  box-shadow: 0 0 5px peru;
}
.respuesta .detalleRespuesta {
  text-align: center;
}
.respuesta div.compraAceptada {
  display: flex;
  justify-content: center;
}
.respuesta .compraAceptada div {
  margin-right: 1em;
}
.respuesta #check {
  fill: none;
  stroke: rgb(56, 167, 56);
  stroke-width: 30px;
  width: 3.5em;
}
.respuesta #x {
  fill: none;
  stroke: red;
  stroke-width: 30px;
  width: 3.5em;
}
.respuesta .detalleCliente {
  margin-right: 2em;
  flex-grow: 1;
  flex-shrink: 0;
  flex-basis: 310px;
}
.respuesta .detalleEntrega {
  text-align: left;
  flex-grow: 2;
  flex-shrink: 1;
  /* flex-basis: 500px; */
}
.respuesta .svgAddress {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 1em 0;
}
.respuesta .svgAddress p {
  text-align: left;
}
.respuesta .svgAddress #placeHolderLocal {
  width: 30px;
  height: auto;
  fill: peru;
  animation: escalar 1s infinite alternate;
}
@keyframes escalar {
  to {
    transform: scale(1);
  }
  from {
    transform: scale(1.3);
  }
}
.respuesta p.horario {
  /* margin-top: 1em; */
  /* text-align: right; */
  font-size: 0.75em;
}
.respuesta .horario span {
  color: #DB9423;
  font-weight: 700;
}
.respuesta .items {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  justify-items: center;
  margin: 2em 0;
  /* padding: 0.5em; */
  /* font-size: 1.5em; */
  border: 0;
  border-radius: 10px;
  box-shadow: 0 0 0 white;
}
.respuesta .items figure.imgPizza {
  grid-column: 1 / span 1;
  font-size: 2em;
  width: 3.5em;
  height: auto;
  overflow: hidden;
  /* border: 1px solid red; */
}
.respuesta .items figure.imgPizza img {
  max-width: 100%;
  height: auto;
  display: block;
  /* border: 1px solid red; */
}
.respuesta .pizza {
  grid-column: 2 / span 1;
  /* width: 10em; */
  /* border: 1px solid red; */
}
.respuesta .promo {
  text-align: center;
  grid-column: 3 / span 1;
}
.respuesta .cantidad {
  grid-column: 3 / span 1;
  /* width: 1.5em; */
  /* border: 1px solid red; */
}
.respuesta .compraRechazada div {
  margin-bottom: 1em;
}
.respuesta p.duda {
  margin: 2em 0;
  text-align: right;
}
.respuesta div {
  text-align: center;
}
.respuesta a .botonContinuar {
  font-size: 1.2em;
  letter-spacing: 0.05em;
  padding: 0.7em 1.5em;
  border-radius: 0.5em;
  color: white;
  outline: none;
  background-color: #DB9423;
}
.respuesta a .botonContinuar:hover {
  cursor: pointer;
  /* background-color: #DB9423; */
}
/* ********************************************************************************************QUERYMIDLETABLET */
@media only screen and (max-width: 768px) {
  .respuesta .detalleRespuesta,
  .respuesta .detalleProductos,
  .respuesta .infoCliente {
    margin: 1em 0;
}
  .respuesta .items {
    grid-template: 2fr 1fr / repeat(2, 1fr);
  }
  .respuesta .items figure.imgPizza {
    grid-area: 1 / 1 / span 1 / span 1;
  }
  .respuesta .pizza {
    grid-area: 2 / 1 / span 1 / span 1;
    text-align: center;
    /* width: 10em; */
    /* border: 1px solid red; */
  }
  .respuesta .promo {
    grid-area: 1 / 2 / span 2 / span 1;
  }
  .respuesta .cantidad {
    grid-area: 1 / 2 / span 2 / span 1;
    /* width: 1.5em; */
    /* border: 1px solid red; */
  }
  .respuesta section {
    margin: 1.5em 0;
  }
  .respuesta section.infoCliente {
    display: block;
  }
  .respuesta .infoCliente .detalleCliente {
    margin: 1.5em 0;
  }
  .respuesta .infoCliente .detalleEntrega {
    margin: 1.5em 0;
  }
  .respuesta p.duda {
    font-size: 0.7em;
  }
}
/* ********************************************************************************************QUERYPHONE */
@media only screen and (max-width: 480px) {
  .respuesta {
    margin: 2em auto;
  }
  .respuesta h2 {
    font-size: 1em;
  }
  .respuesta p {
    font-size: 0.8em;
  }
  .respuesta p.horario {
    font-size: 0.6em;
  }
  .respuesta .items figure.imgPizza {
    width: 3em;
  }
}