header {
  /* background-color: #ffffff;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1; */
  /* border: 1px red solid; */
}
header a img {
  margin: 1% 0 1% 6%;
  float: left;
  width: 8em;
  /* border: 1px red solid; */
}
header nav:after {
  content: "";
  clear: both; /*elemento es movido hacia abajo para limpiar tanto elementos flotntes de la izquierda como la derecha*/
  display: table;
}
header nav ul {
  float: right;
  margin-right: 5em;
  list-style: none;
  position: relative;
  /* border: 1px red solid; */
}
header nav ul li {
  float: left;
  margin: 1.7em 1em;
  background-color: white;
  /* border: 1px red solid; */
}
header nav ul li a {
  color: rgb(0, 0, 0);
  text-decoration: none;
  line-height: 3em;
  font-size: 1em;
  letter-spacing: 0.05em;
}
header nav ul li a.despues::after {
  content: "\02C5";
  margin-left: 0.4em;
}
header nav ul li a:hover {
  color: #DB9423;
  transition: color 50ms ease-in-out;
}
header nav ul li a[tabindex]:focus {
  color:#DB9423;
  /* outline: none; */
}
header nav ul li ul.subMenu {
  position: absolute;
  top: 80px;
  visibility: hidden; 
  transition: top 300ms; /*primero aparece en 90 y luego en 80px*/
}
header nav ul li:hover ul.subMenu {
  top: 70px;
  visibility: visible;
  border-top: 1px solid #DB9423;
}
header nav ul li ul.subMenu li {
  position: relative;
  margin: 0;
  padding-left: 1em; 
  width: 12em;
  float: none;
  display: list-item;
}
.navega button:hover {
  cursor: pointer;
  background-color: #01578C;
}
.muestra, .iconBarra, input.inputHeader {
  display: none;
}
/* Next three selectors are for carrito SVG */
header li.carrito {
  margin: 2.5em 1em;
  padding: auto;
  width: 20px;
}
#Layer_2 {
  display: block;
  stroke: peru;
  margin: auto 0;
  /* filter: opacity(30%) brightness(10%); */
  /* animation: escalar 1s infinite alternate; */
  /* border: 1px solid red; */
}
