  }
  from {
    transform: scale(1.5);
  }
} */
@media only screen and (max-width: 1220px) {
  header nav {
    margin-right: 2em;
  }
}
/* ********************************************************************************************QUERYTABLET */
@media only screen and (max-width: 1024px) {
  header nav ul li a {
    margin: 0;
    margin-left: 2%;
    /* border: 1px red solid; */
  }
  header nav ul {
    width: 100%;
    float: left;
    /* border: 1px red solid; */
  }
  header nav ul li, header nav ul li ul.subMenu li {
    display: block;
    margin: 0;
    padding: 0;
    width: 100%;
    /* border: 1px red solid; */
  }
  header nav ul li ul.subMenu {
    top: 70px;
    position: static;
    border-top: none;
    float: none;
    display: none;
    opacity: 1;
    visibility: visible;
    /* border: 1px red solid; */
  }
  header nav ul li ul.subMenu li a {
    margin-left: 7%;
  }
  .muestra {
    display: block;
    color: rgb(0, 0, 0);
    font-size: 1em;
    letter-spacing: 0.05em;
    margin-left: 2%;
    line-height: 3em;
    cursor: pointer;
    /* border: 1px red solid; */
  }
  .muestra:hover {
    color: #DB9423;
    transition: color 50ms ease-in-out;
  }
  .iconBarra {
    display: block;
    position: absolute;
    right: 40px;
    margin-top: 20px;
    font-size: 25px;
    cursor: pointer;
    /* border: 1px red solid; */
  }
  .muestra + a, nav > ul {
    display: none;
  }
  #boton:checked + nav > ul, 
  #boton-conoce:checked + ul.subMenu, 
  #boton-especialidad:checked + ul.subMenu {
    display: block;
  }
  header li.carrito {
    margin: 1em 1em;
    width: 35px;
  }
  #Layer_2{
    margin-top: -55px;
  }
}
