.carrito {
  width: 90%;
  margin: 4em auto;
}
.carrito .vacio {
  margin: 3em auto;
  text-align: center;
}
.carrito .vacio a {
  color: #DB9423;
  text-decoration: none;
}
.carrito .vacio a:hover {
  color: #db5d23;
}
.carrito .sNavigation {
  margin-bottom: 2.5em;
  text-align: center;
  font-size: 1.5em;
  /* border: 1px solid red; */
}
.carrito .sNavigation .breadcrumb > li {
  display: inline;
}
.carrito .sNavigation .breadcrumb li+li::before {
  padding: 10px;
  content: ">";
}
.carrito .sNavigation .breadcrumb .car {
  color: #DB9423;
  font-weight: 500;
}
.carrito .items {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 1em 0;
  padding: 0.5em;
  font-size: 1.5em;
  border: 2px solid #db952377;
  border-radius: 10px;
  box-shadow: 10px 10px 5px #DB9423;
}
.carrito .items figure.imgPizza {
  width: 3.5em;
  height: auto;
  overflow: hidden;
  /* border: 1px solid red; */
}
.carrito .items figure.imgPizza img {
  max-width: 100%;
  height: auto;
  display: block;
  /* border: 1px solid red; */
}
.carrito .pizza {
  width: 10em;
  text-align: center;
  /* border: 1px solid red; */
}
.carrito .namePromo {
  width: 7em;
  text-align: center;
}
.carrito .promo {
  width: 12.5em;
}
.carrito .cantidad {
  width: 1.5em;
  /* border: 1px solid red; */
}
.carrito .precio {
  width: 5em;
  /* border: 1px solid red; */
}
.carrito .precioCantidad {
  font-weight: 700;
  width: 5em;
  /* border: 1px solid red; */
}
.carrito .total {
  margin-top: 2em;
  text-align: right;
  font-size: 2em;
  font-weight: 800;
  color: #DB9423;
}
.carrito p.horario {
  margin-top: 1em;
  text-align: right;
  font-size: 0.75em;
}
.carrito .horario span {
  color: #DB9423;
  font-weight: 700;
}
.carrito div {
  text-align: center;
}
.carrito a .botonContinuar {
  font-size: 1.2em;
  letter-spacing: 0.05em;
  margin: 4em 0 1em 0;
  padding: 0.7em 1.5em;
  border-radius: 0.5em;
  border-width: 3px;
  color: white;
  outline: none;
  background-color: #DB9423;
}
.carrito a .botonContinuar:hover {
  cursor: pointer;
  /* background-color: #DB9423; */
}
/* ********************************************************************************************QUERYMIDLETABLET */
@media only screen and (max-width: 790px) {
  .carrito .items {
    /* display: flex; */
    display: grid;
    grid-template: repeat(2, 1fr) / 1fr 2fr 1fr;
    justify-items: center;
    align-items: center;
    text-align: center;
    font-size: 1em;
  }
  .carrito .items figure {
    /* order: 1; */
    grid-area: 1 / 1 / span 1 / span 1;
  }
  .carrito .items .pizza {
    /* order: 3; */
    grid-area: 1 / 2 / span 1 / span 1;
  }
  .carrito .items .namePromo {
    width: auto;
    grid-area: 2 / 1 / span 1 / span 1;
    /* order: 4; */
  }
  .carrito .items .promo {
    width: 10em;
    grid-area: 1 / 2 / span 2 / span 1;
    /* order: 4; */
  }
  .carrito .items .cantidad {
    /* order: 4; */
    grid-area: 2 / 1 / span 1 / span 1;
  }
  .carrito .items .precio {
    /* order: 2; */
    grid-area: 2 / 2 / span 1 / span 1;
  }
  .carrito .items .precioCantidad {
    /* order: 5; */
    grid-area: 1 / 3 / span 2 / span 1;
    font-size: 1em;
  }
}
/* ********************************************************************************************QUERYPHONE */
@media only screen and (max-width: 480px) {
  .carrito .sNavigation {
    font-size: 1.1em;
    /* border: 1px solid red; */
  }
  .carrito .items {
    font-size: 0.9em;
  }
  .carrito .items .precioCantidad {
    font-size: 1em;
  }
  .carrito .total {
    font-size: 1.5em;
  }
  .carrito p.horario {
    font-size: 0.6em;
  }
}