.formikUser .who,
.formikUser .deliLocal,
.formikUser .address {
  margin-bottom: 3em;
}
.formikUser h2 {
  margin-bottom: 0.5em;
}
.formikUser form input,
.formikUser form select,
.formikUser form textarea {
  padding: 0.3em;
  font-size: 1.2em;
  border: 2px solid gray;
  border-radius: 3px;
  transition-property: border;
  transition-duration: 400ms;
  transition-timing-function: ease-in;
}
.formikUser form input:focus,
.formikUser form select:focus,
.formikUser form textarea:focus {
  outline: none;
  border: 2px solid rgb(74, 74, 74);
  box-shadow: 0 0 1.5px 1.5px rgb(74, 74, 74);
  /* background-color: blanchedalmond; */
}
.formikUser form .who .bloque1,
.formikUser form .address .bloque1 {
  display: flex;
  width: 100%;
  /* border: 1px solid red; */
}
.formikUser form .address .infoSelect {
  /* display: flex; */
  margin-bottom: 1em;
  /* border: 1px solid red; */
}
.formikUser form .address .infoSelect .infoSelectBloque1,
.formikUser form .address .infoSelect .infoSelectBloque2 {
  display: flex;
}
.formikUser form .address .infoSelect .infoSelectBloque1 .inputPais,
.formikUser form .address .infoSelect .infoSelectBloque2 .inputProvincia {
  margin-right: 1em;
  margin-bottom: 0.5em;
}
.formikUser form .who .bloque1 .inputNombres .campoNombres,
.formikUser form .who .bloque1 .inputApellidos .campoApellidos,
.formikUser form .address .infoSelect section #campoPais,
.formikUser form .address .infoSelect section #campoDepartamento,
.formikUser form .address .infoSelect section #campoProvincia,
.formikUser form .address .infoSelect section #campoDistrito,
.formikUser form .address .bloque1 .inputUrbanizacion .campoUrbanizacion,
.formikUser form .address .bloque1 .inputTelefono .campoTelefono,
.formikUser form .address .bloque1 .inputNumero .campoNumero,
.formikUser form .address .bloque1 .inputApartamento .campoApartamento,
.formikUser form .address .bloque1 .inputBloque .campoBloque {
  width: 100%;
}
.formikUser form .address section {
  width: 100%;
}
.formikUser form .who .bloque1 .inputNombres, 
.formikUser form .address .bloque1 .inputUrbanizacion {
  width: 100%;
  margin: 0 1em 1em 0;
}
.formikUser form .address .bloque1 .inputApartamento {
  margin: 0 1em;
}
.formikUser form .who .bloque1 .inputApellidos,
.formikUser form .address .bloque1 .inputTelefono,
.formikUser form .address .bloque1 .inputNumero,
.formikUser form .address .bloque1 .inputApartamento,
.formikUser form .address .bloque1 .inputBloque {
  width: 100%;
  margin-bottom: 1em;
}
.formikUser form .deliLocal .bloqueEntrega label {
  margin-right: 2em;
}
.formikUser form .deliLocal .bloqueEntrega input {
  margin-right: 0.3em;
}
.formikUser form .campoEmail,
.formikUser form .campoCalle,
.formikUser form .campoDetalles {
  width: 100%;
}
.formikUser form .inputCalle {
  margin-bottom: 1em;
}
.formikUser form .campoDetalles {
  height: 5em;
}
.formikUser form .svgAddress {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 2em;
}
.formikUser form .svgAddress #placeHolderLocal {
  width: 30px;
  height: auto;
  fill: peru;
  animation: escalar 1s infinite alternate;
}
@keyframes escalar {
  to {
    transform: scale(1);
  }
  from {
    transform: scale(1.3);
  }
}
.formikUser button {
  font-size: 1.2em;
  letter-spacing: 0.05em;
  /* margin: 4em 0 1em 0; */
  padding: 0.7em 1.5em;
  border-radius: 0.5em;
  border-width: 3px;
  color: white;
  outline: none;
  background-color: #DB9423;
}
.formikUser button:hover {
  cursor: pointer;
}
.formikUser div {
  color: red;
}
/* ********************************************************************************************QUERYPHONE */
@media only screen and (max-width: 480px) {
  .formikUser form input,
  .formikUser form select,
  .formikUser form textarea {
    font-size: 1em;
  }
  .formikUser form .address .bloque1 .inputNumero {
    flex-shrink: 2;
  }
  .formikUser form .address .bloque1 .inputApartamento {
    flex-shrink: 1;
    flex-basis: 180px;
  }
  .formikUser form .address .bloque1 .inputBloque {
    flex-shrink: 2;
  }
}
@media only screen and (max-width: 372px) {
  .formikUser form .address .infoSelect {
    display: block;
  }
  .formikUser form .address .infoSelect .infoSelectBloque1,
  .formikUser form .address .infoSelect .infoSelectBloque2,
  .formikUser form .address .bloque1 {
    display: block;
  }
  .formikUser form .address .infoSelect .infoSelectBloque1 .inputDepartamento {
    margin-bottom: 0.5em;
  }
  .formikUser form .address .bloque1 .inputApartamento {
    margin: 1em 0;
  }
}
