.MyV .info {
  margin: auto;
  padding: 3em 0 0 0;
  text-align: center;
  width: 50%;
  /* border: 1px solid red; */
}
.MyV .info h2{
  font-size: 3em;
}
.MyV .info p{
  margin: 0 auto;
  width: 90%;
}
.MyV .video {
  margin: 4em auto;
  width: 50%;
  height: auto;
  overflow: hidden;
  box-shadow: 0 0 15PX #000000;
  border-radius: 1em;
  /* border: 1px solid red; */
}
.MyV .video video {
  max-width: 100%;
  height: auto;
  display: block;
  /* border: 1px solid red; */
}
@media only screen and (max-width: 768px) {
  .MyV .info {
    text-align: center;
    width: 90%;
  }
  .MyV .video {
    width: 90%;
  }
}
