.promociones {
  margin: 4em auto;
}
.promociones .loading {
  display: flex;
  justify-content: center;
}
.promociones .promocionesInside {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 4em 2.5em;
  margin: 4em auto;
  width: 90%;
  text-align: center;
  /* border: 1px solid red; */
}
.promociones .loading h2 {
  font-size: 2em;
  animation: opacity 1s ease-in-out infinite alternate;
}
.promociones [class="Pack Familiar 1"] {
  grid-column: 1 / span 2;
}
.promociones [class="Pack Familiar 2"] {
  grid-column: 3 / span 2;
}
.promociones [class="Promo para Compartir"] {
  grid-column: 5 / span 2;
}
.promociones [class="Dúos Grandes"] {
  grid-column: 2 / span 2;
}
.promociones [class="Promo Entre Patas"] {
  grid-column: 4 / span 2;
}
.promociones .promocionesInside .pizza div,
.promociones .promocionesInside .bebida div {
  color: red;
}
.promociones .promocionesInside .pizza,
.promociones .promocionesInside .bebida {
  margin: 1em 0;
}
.promociones .promocionesInside .pizza .inputPizza {
  text-align: center;
  text-align-last: center;
}
.promociones .promocionesInside .bebida .inputBebida {
  text-align: center;
  text-align-last: center;
}
.promociones .promocionesInside .details {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.5em auto;
  /* border: 1px solid red; */
}
.promociones .promocionesInside .details p.precio {
  margin-right: 0.5em;
  text-align: right;
}
.promociones .promocionesInside .details button {
  margin: 0 0.7em;
  background-color: rgb(228, 227, 227);
  border: none;
  outline: none;
  box-shadow: 0 0 5PX #DB9423;
}
.promociones .promocionesInside .details button:hover {
  cursor: pointer;
  box-shadow: inset 0 0 5PX #DB9423;
}
.promociones .promocionesInside .details button.mas {
  padding: 0 0.5em;
}
.promociones .promocionesInside div.menos {
  display: flex;
  margin: 0 0.7em;
  height: 1em;
  padding: 0 7.2px;
  background-color: rgb(228, 227, 227);
  box-shadow: 0 0 5PX #DB9423;
}
.promociones .promocionesInside div.menos span {
  margin-top: -4.6px;
}
.promociones .promocionesInside div.menos:hover {
  cursor: pointer;
  box-shadow: inset 0 0 5PX #DB9423;
}
.promociones .promocionesInside p.counter {
  margin: 0 0 0 0.5em;
  padding: 0;
  width: 2.6em;
  text-align: center;
  font-weight: 800;
  color: #db5d23;
  /* border: 1px solid red; */
}
.promociones .promocionesInside figure.imgPromo {
  margin: 0 auto;
  width: 17em;
  height: auto;
  overflow: hidden;
  /* border: 1px solid red; */
}
.promociones .promocionesInside figure.imgPromo img {
  max-width: 100%;
  height: auto;
  display: block;
  /* border: 1px solid red; */
}
.promociones div {
  text-align: center;
}
.promociones a .botonCarrito {
  font-size: 1.2em;
  letter-spacing: 0.05em;
  margin: 0 auto 2em auto;
  padding: 0.7em 1.5em;
  border-width: 3px;
  border-radius: 0.5em;
  color: white;
  outline: none;
  background-color: #DB9423;
}
.promociones .botonCarrito:hover {
  cursor: pointer;
  background-color: #DB9423;
}
/* ************************************************************************QUERYTablet */
@media only screen and (max-width: 1050px) {
  .promociones {
    grid-gap: 3em 1.5em;
  }
  .promociones [class="Pack Familiar 1"] {
    grid-column: 1 / span 3;
  }
  .promociones [class="Pack Familiar 2"] {
    grid-column: 4 / span 3;
  }
  .promociones [class="Promo para Compartir"] {
    grid-column: 1 / span 3;
  }
  .promociones [class="Dúos Grandes"] {
    grid-column: 4 / span 3;
  }
  .promociones [class="Promo Entre Patas"] {
    grid-column: 3 / span 2;
  }
  .promociones .promocionesInside .details {
    margin: 2em auto;
  }
  .promociones .promocionesInside .details button {
    font-size: 1em;
  }
  .promociones .promocionesInside .details button.mas {
    padding: 0.5em 1em;
  }
  .promociones .promocionesInside div.menos {
    display: block;
    height: auto;
    width: auto;
    padding: 6.1px 17.6px;
  }
}
/* ************************************************************************QUERYMIDLETABLET */
@media only screen and (max-width: 790px) {
  .promociones [class="Pack Familiar 1"] {
    grid-column: 1 / span 6;
  }
  .promociones [class="Pack Familiar 2"] {
    grid-column: 1 / span 6;
  }
  .promociones [class="Promo para Compartir"] {
    grid-column: 1 / span 6;
  }
  .promociones [class="Dúos Grandes"] {
    grid-column: 1 / span 6;
  }
  .promociones [class="Promo Entre Patas"] {
    grid-column: 1 / span 6;
  }
  .promociones .promocionesInside .details {
    /* flex-direction: column; */
    margin: 2em auto;
  }
  .promociones .promocionesInside .details button {
    margin: 0 0.6em;
  }
  .promociones .promocionesInside .details p.counter {
    margin: 0 0 0 0.4em;
  }
}
