.recomendacion {
  display: flex;
  justify-content: space-around;
  margin: 5em auto;
  width: 90%;
  /* border: 1px solid red; */
}
.recomendacion .pizza {
  text-align: center;
  /* border: 1px solid red; */
}
.recomendacion .pizza h2 {
  font-size: 2.5em;
}
.recomendacion .pizza p {
  margin: 2em 0;
}
.recomendacion .imagen {
  margin: auto 0;
}
.recomendacion .imagen figure {
  width: 100%;
  height: auto;
  overflow: hidden;
  /* border: 1px solid red; */
}
.recomendacion .imagen img {
  max-width: 100%;
  height: auto;
  display: block;
  /* border: 1px solid red; */
}
/* ************************************************************************QUERYmiddleTablet */
@media only screen and (max-width: 768px) {
  .recomendacion {
    display: block;
  }
  .recomendacion .imagen figure {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 70%;
  }
}
/* ************************************************************************QUERYPHONE */
@media only screen and (max-width: 480px) {
  .recomendacion {
    margin: 1em auto 4em auto;
    /* border: 1px solid red; */
  }
  .recomendacion .pizza h2 {
    font-size: 1.5em;
  }
}