.contactoInfo {
  text-align: center;
}
.contactoInfo .section1, .contactoInfo .section2 {
  /* border: 1px solid red; */
  margin: 4em 0 1em 0;
}
.contactoInfo .section3 {
  margin: 1em 0 4em 0;
}
.contactoInfo .section1 p, .contactoInfo .section2 p, .contactoInfo .section3 p {
  margin: 1em auto 0 auto;
  width: 90%;
  /* border: 1px solid red; */
}
.contactoInfo .horarios {
  margin: 1em;
  /* border: 1px solid red; */
}
.contactoInfo .horarios .hora{
  display: flex;
  justify-content: center;
  /* border: 1px solid red; */
}
.contactoInfo .horarios .hora p {
  margin: 0.5em;
  font-weight: 600;
}
.contactoInfo .horarios .hora .horaEspecial {
  color: #DB9423;
}
.contactoInfo .horarios .hora .dia{
  width: 4.5em;
  text-align: right;
}