.infoUser {
  /* display: flex;
  justify-content: space-around; */
  /* align-items: center; */
  width: 90%;
  margin: 4em auto;
  /* border: 1px solid red; */
}
.infoUser .sNavigation {
  margin-bottom: 2.5em;
  text-align: center;
  font-size: 1.5em;
  /* border: 1px solid red; */
}
.infoUser .sNavigation .breadcrumb > li {
  display: inline;
}
.infoUser .sNavigation .breadcrumb li.contacto::before {
  color: black;
}
.infoUser .sNavigation .breadcrumb li+li::before {
  padding: 10px;
  content: ">";
}
.infoUser .sNavigation .breadcrumb .contacto {
  color: #DB9423;
  font-weight: 500;
}
.infoUser .formResumen {
    display: flex;
  justify-content: space-around;
}
.infoUser .resumen {
  margin-left: 2em;
}
/* ********************************************************************************************QUERYTABLET */
@media only screen and (max-width: 1024px) {
  .infoUser .formResumen {
    flex-direction: column-reverse;
  }
  .infoUser .resumen {
    margin: 0;
  }
}
/* ********************************************************************************************QUERYPHONE */
@media only screen and (max-width: 480px) {
  .infoUser .sNavigation {
    font-size: 1.1em;
    /* border: 1px solid red; */
  }
}