.complementos {
  margin: 4em auto;
}
.complementos .loading {
  display: flex;
  justify-content: center;
}
.complementos .loading h2 {
  font-size: 2em;
  animation: opacity 1s ease-in-out infinite alternate;
}
@keyframes opacity{
  to {
    opacity: 1;
  }
  from {
    opacity: 0;
  }
}
.complementos .complementosInside {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 4em 2.5em;
  margin: 4em auto;
  width: 90%;
  text-align: center;
  /* border: 1px solid red; */
}
.complementosInside .ajo {
  grid-column: 1 / span 2;
}
.complementosInside .especial {
  grid-column: 3 / span 2;
}
.complementosInside .Calzone {
  grid-column: 1 / span 2;
}
.complementosInside .Lasagna {
  grid-column: 3 / span 2;
}
.complementosInside h2 {
  margin: 1em 0;
}
.complementosInside .details {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.5em auto;
  /* border: 1px solid red; */
}
.complementosInside .details p.precio {
  margin-right: 0.5em;
  text-align: right;
}
.complementosInside .details button {
  margin: 0 0.7em;
  background-color: rgb(228, 227, 227);
  border: none;
  outline: none;
  box-shadow: 0 0 5PX #DB9423;
}
.complementosInside .details button:hover {
  cursor: pointer;
  box-shadow: inset 0 0 5PX #DB9423;
}
.complementosInside .details button.mas {
  padding: 0 0.5em;
}
.complementosInside .details button.menos {
  padding: 0 0.6em;
}
.complementosInside .details p.counter {
  margin: 0 0 0 0.5em;
  padding: 0;
  width: 2.6em;
  text-align: center;
  font-weight: 800;
  color: #db5d23;
  /* border: 1px solid red; */
}
.complementosInside figure.imgPizza {
  margin: 0 auto;
  width: 15em;
  height: auto;
  overflow: hidden;
  /* border: 1px solid red; */
}
.complementos .complementosInside figure.imgPizza img {
  max-width: 100%;
  height: auto;
  display: block;
  /* border: 1px solid red; */
}
.complementos div {
  text-align: center;
}
.complementos a .botonCarrito {
  font-size: 1.2em;
  letter-spacing: 0.05em;
  margin: 0 auto 2em auto;
  padding: 0.7em 1.5em;
  border-width: 3px;
  border-radius: 0.5em;
  color: white;
  outline: none;
  background-color: #DB9423;
}
.complementos .botonCarrito:hover {
  cursor: pointer;
  background-color: #DB9423;
}
/* ************************************************************************QUERYTablet */
@media only screen and (max-width: 1050px) {
  .complementosInside {
    grid-gap: 3em 1.5em;
  }
  .complementosInside .details {
    margin: 2em auto;
  }
  .complementosInside .details button {
    font-size: 1em;
  }
  .complementosInside .details button.mas {
    padding: 0.5em 1em;
  }
  .complementosInside .details button.menos {
    padding: 0.5em 1.1em;
  }
}
/* ************************************************************************QUERYMIDLETABLET */
@media only screen and (max-width: 790px) {
  .complementosInside {
    grid-gap: 3em 1.5em;
  }
  .complementosInside .ajo {
    grid-column: 1 / span 4;
  }
  .complementosInside .especial {
    grid-column: 1 / span 4;
  }
  .complementosInside .Calzone {
    grid-column: 1 / span 4;
  }
  .complementosInside .Lasagna {
    grid-column: 1 / span 4;
  }
  .complementosInside .details {
    /* flex-direction: column; */
    margin: 2em auto;
  }
  .complementosInside .details button {
    margin: 0 0.6em;
  }
  .complementosInside .details p.counter {
    margin: 0 0 0 0.4em;
  }
}
/* ************************************************************************QUERYPHONE */
@media only screen and (max-width: 480px) {
  .complementos h2 {
    font-size: 1.5em;
  }
}