section.portadaConocenos {
  padding: 4em 0 5em 0;
  text-align: center;
  background-image: linear-gradient(to bottom, rgba(117, 117, 117, 0.671), rgba(0, 0, 0, 0.85)),
  url(../../images/portadas/pizzarelaConocenos.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
section.portadaConocenos h1 {
  margin: 0 0 0.5em 0;
  font-size: 6em;
  color: white;
  /* border: 1px solid red; */
}
section.portadaConocenos p {
  margin: auto;
  width: 90%;
  color: white;
  /* border: 1px solid red; */
}
/* ************************************************************************QUERYPHONE */
@media only screen and (max-width: 480px) {
  section.portadaConocenos {
    padding: 3em 0 5em 0;
  }
  section.portadaConocenos h1 {
    /* margin: 0.5em 0; */
    font-size: 3.5em;
    /* border: 1px solid red; */
  }
}