section.inicio {
  padding: 4em 0 5em 0;
  text-align: center;
  background-image: linear-gradient(to bottom, rgba(117, 117, 117, 0.85), rgba(0, 0, 0, 0.85)),
  url(../../../images/portadas/Pizzarela.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
section.inicio h2 {
  font-size: 1.5em;
  color: #DB9423;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
section.inicio h1 {
  font-family: Georgia, 'Times New Roman', Times, serif;
  margin: 0.9em 0;
  font-size: 6em;
  color: white;
  /* border: 1px solid red; */
}
section.inicio p {
  margin: auto;
  width: 90%;
  color: white;
}
section.inicio div.divButton {
  display: flex;
  justify-content: center;
  /* border: 1px red solid; */
}
section.inicio .divButton div button {
  font-size: 1.2em;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif ;
  letter-spacing: 0.05em;
  margin: 2em 1em 0 1em;
  padding: 0.7em 1.5em;
  border-radius: 2em;
  border-width: 0;
  color: white;
  background-color: #DB9423;
  /* outline:none; */
  /* border: 5px none; */
  /* border: 1px red solid; */
}
section.inicio .divButton div button:hover {
  cursor: pointer;
  color: black;
  box-shadow: 0 0 15PX #DB9423;
  background-color: #DB9423;
}
/* ************************************************************************QUERYMIDLETABLET */
@media only screen and (max-width: 790px) {
  section.inicio div.divButton {
    display: block;
    /* border: 1px red solid; */
  }
}
/* ************************************************************************QUERYPHONE */
@media only screen and (max-width: 480px) {
  section.inicio {
    padding: 3em 0 5em 0;
  }
  section.inicio h1 {
    /* margin: 0.5em 0; */
    font-size: 4em;
    /* border: 1px solid red; */
  }
}